export default {
	data() {
		return {
			useUser:this.user,
			userArr:[],
			url:this.url1
		}
	},
	onLoad(options) {
	    this.myTeam()
	},
	methods: {
		back : function(){
		   uni.navigateBack({
		
		   });
		},
			
		myTeam:function(){
			let that=this;
			let usurl1=that.url1+"/dinghai/user/selectMy";
			uni.request({
			    url: usurl1,
				header:{
					"Content-Type" : "application/x-www-form-urlencoded"
				},
				method:'POST',
			    data: {
					introduce:that.useUser.tel
				},
			    success: (res) => {
					if(res.statusCode==200){
						this.userArr=res.data.flag
					}
			    },
				fail : function(err){
					console.log(err);
				}
			})
		}
	}
}
